import React, { Component} from 'react';

class ToLogin extends Component {
	render() {
		return (
			<div className="g-bd g-bd-full">
				<div className="n-pglg2">
					<div className="welc s-fc3 f-fs1">
						你可以关注明星和好友品味他们的私房歌单<br />
						通过他们的动态发现更多精彩音乐
					</div>
					<a href="#" className="btn">立即登录</a>
				</div>
			</div>
		);
	}
}

export default ToLogin


